import React, { Component } from 'react';
import '../../../static/style/SillyStoreGenerator.css';

class SillyStoryGenerator extends Component {
  constructor(props) {
    super(props);
    this.customNameRef = React.createRef();
    this.state = {
      selectedValue: 'us',
      isShowStory: false,
      story: '',
    };
  }

  handleGenerate = () => {
    function randomValueFromArray(array) {
      return array[Math.floor(Math.random() * array.length)];
    }

    const customName =
      this.customNameRef.current.value === '' ? 'Bob' : this.customNameRef.current.value;
    console.log(customName);

    const insertx = ['Willy the Goblin', 'Big Daddy', 'Father Christmas'];
    const inserty = ['the soup kitchen', 'Disneyland', 'the White House'];
    const insertz = [
      'spontaneously combusted',
      'melted into a puddle on the sidewalk',
      'turned into a slug and crawled away',
    ];
    const temperature = 94;
    const weight = 300;
    const temperatureString =
      { ...this.state }.selectedValue === 'us'
        ? `${temperature} fahrenheit`
        : `${Math.round(((temperature - 32) * 5) / 9)} centigrade`;
    const weightString =
      { ...this.state }.selectedValue === 'us'
        ? `${weight} pounds`
        : `${Math.round(weight / 14)} stones`;

    const story = `It was ${temperatureString} outside, so ${randomValueFromArray(
      insertx
    )} went for a walk. When they got to ${randomValueFromArray(
      inserty
    )}, they stared in horror for a few moments, then ${randomValueFromArray(
      insertz
    )}. ${customName} saw the whole thing, but was not surprised â€” ${randomValueFromArray(
      insertx
    )} weighs ${weightString}, and it was a hot day.`;

    this.setState({
      isShowStory: true,
      story,
    });
  };

  handleCheck = event => {
    const check = event.target.value;
    this.setState({
      selectedValue: check,
    });
  };

  showStory = () => {
    const { story, isShowStory } = { ...this.state };
    if (isShowStory) {
      return story;
    }
    return '';
  };

  render() {
    return (
      <div className="container-fluid body float-left">
        <div className="sillyd">
          <label className="sillyl" htmlFor="customname">
            Enter custom name:
          </label>
          <input className="sillyi" type="text" placeholder="" ref={this.customNameRef} />
        </div>
        <div className="sillyd">
          <label className="sillyl" htmlFor="us">
            US
            <input
              className="sillyi"
              id="us"
              type="radio"
              name="ukus"
              value="us"
              onChange={this.handleCheck}
              defaultChecked
            />
          </label>
          <label className="sillyl" htmlFor="uk">
            UK
            <input
              className="sillyi"
              id="uk"
              type="radio"
              name="ukus"
              value="uk"
              onChange={this.handleCheck}/>
          </label>
        </div>
        <div className="sillyd">
          <button onClick={this.handleGenerate}>Generate random story</button>
        </div>

        <p className="story">{this.showStory()}</p>
      </div>
    );
  }
}

export default SillyStoryGenerator;
